import snabbdom from '@servicenow/ui-renderer-snabbdom';
import {createCustomElement} from '@servicenow/ui-core';
import resizeBehavior from '@servicenow/behavior-resize';
import { t } from 'sn-translate';
import style from './snTruncateText.scss';

import '@servicenow/now-button';
import * as utils from './utils/methods';
import * as ACTIONS from './utils/constants';

const _renderExpandButton = (showButtons, expanded, dispatch) => {
	if (showButtons) {
		return (
			<div className="expand-buttons">
				<now-button-bare
					className="now-m-block-start--xxs"
					on-click={() => {
						dispatch(ACTIONS.TEXT_EXPANDED, {value: !expanded});
					}}
					variant="secondary"
					hide-padding
					high-contrast
					label={expanded ? t('Show less') : t('Show more')}
					config-aria={{
						'aria-hidden': 'true'
					}}
				/>
			</div>
		);
	} else {
		return '';
	}
};

const view = (state, { dispatch }) => {
	const {width} = state.behaviors.resize.size;
	const {expanded} = state;
	const {text} = state.properties;
	const availableWidth = utils.getWidth(width);
	const showButtons = utils.isOversizedText(text, availableWidth);
	return (
		<div>
			<div
				class={{
					'truncate-text': true,
					'is-expanded': expanded
				}}>
				<span className="text">
					{ text }
				</span>
			</div>
			{_renderExpandButton(showButtons, expanded, dispatch)}
		</div>
	);
};

export default createCustomElement('sn-truncate-text', {
	initialState: {
		expanded: false
	},

	renderer: {
		type: snabbdom,
		view
	},

	behaviors: [
		{
			behavior: resizeBehavior,
			options: {timer: {wait: 200}}
		},
	],

	styles: style,

	actionHandlers: {
		[ACTIONS.TEXT_EXPANDED] :({updateState, state, action})=> {
			const { value } = action.payload;
			updateState({
				...state,
				expanded: value
			});
		}
	},

	properties: {
		text: '',
	},
});
