import {measureTextFactory} from '@servicenow/library-enhanced-utils';

const MIN_WIDTH = '288';
const mt = measureTextFactory('16px Source Sans Pro,sans-serif');

const measureText = (text) => {
	return Math.ceil(mt(text));
};

export function getWidth (width) {
    if (typeof width === 'number') {
		//Getting of width of two rows 
		return 2*Math.max(width, MIN_WIDTH);
	}
}
export function isOversizedText (text, availableWidth) {
	if (!availableWidth) 
		return false;
	
	let textWidth = 0;
	let TOLERANCE = 15;
	if (text)
		textWidth = measureText(text);
	return textWidth > (availableWidth - TOLERANCE);
}
